.product-detail-main {
  .single-review-z-index {
    z-index: 1025;
  }

  .product-detail-left {
    .prod-plus-minus {
      width: 35px;
    }

    @media screen and (min-width: 1024px) {
      position: sticky;
      height: 100%;
      top: 150px;
    }

    .slider-main {
      min-height: 400px;
      background-color: var(--white);
      border-radius: 25px;
      box-shadow: 0px 50px 50px #00000008;
      @media screen and (width: 1024px) {
        min-height: 400px;
      }

      .slider-main-inner-div {
        min-height: 450px;
        @media screen and (width: 1024px) {
          min-height: 400px;
        }
        @media screen and (max-width: 576px) {
          min-height: 450px;
        }
      }

      .product-vertical-slider {
        max-height: 400px;

        .product-img {
          height: 100px;
          width: 100px;
          margin: 0 auto;
          border: 2px solid transparent;
          border-radius: 15px;
          background-color: var(--white);
          margin-bottom: 15px;
          box-shadow: 0 5px 3px 0 rgba(0, 0, 0, 0.04), 0 1px 2px 0 rgba(0, 0, 0, 0.06) !important;

          img {
            border-radius: 15px;
          }
        }

        .slick-current {
          .product-img {
            border: 2px solid var(--green) !important;
          }
        }
      }

      .product-hori-slider-main {
        position: relative;
        min-height: 360px;
        @media screen and (width: 1024px) {
          min-height: 300px;
        }

        .product-hori-slider {
          max-height: 400px;
          height: 100%;

          img {
            height: 350px;
            object-fit: contain;
          }
        }

        .like {
          position: absolute;
          top: 0;
          right: 15px;
          @media screen and (max-width: 576px) {
            position: absolute;
            //left: 285px;
            top: unset;
            bottom: -90px;
            text-align: center;
            right: 0;
            z-index: 1;
          }
        }

        .expand-view {
          position: absolute;
          left: 0;
          bottom: 0;
        }

        .product-download {
          position: absolute;
          right: 0;
          bottom: 0;
        }
      }

    }

    .slick-dots {
      bottom: 6px !important;

      li {
        width: 10px;
        margin-right: 0;

        button {
          background-color: #B8BBC4;
          height: 6px;
          width: 6px;
          padding: 3px;
          border-radius: 50%;
          display: inline-block;

          &:before {
            content: none;
          }
        }
      }

      .slick-active {
        width: 30px;

        button {
          background-color: var(--yellow);
          height: 0;
          width: 30px;
          border-radius: 15px;
        }
      }
    }
