.addresss-change-modal {
  /* The radio-main */
  .address-radio-main {
    display: block;
    position: relative;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }

  /* Hide the browser's default radio button */
  .address-radio-main input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
  }

  /* Create a custom radio button */
  .checkmark {
    position: absolute;
    top: 3px;
    right: 0;
    left: auto;
    height: 20px;
    width: 20px;
    background-color: var(--white);
    border: 2px solid #73788B;
    border-radius: 50%;
  }

  /* On mouse-over, add a grey background color */
  .address-radio-main:hover input ~ .checkmark {
    background-color: var(--white);
  }

  /* When the radio button is checked, add a blue background */
  .address-radio-main input:checked ~ .checkmark {
    background-color: var(--white);
    border: 2px solid #0AAF51;
  }

  /* Create the indicator (the dot/circle - hidden when not checked) */
  .checkmark:after {
    content: "";
    position: absolute;
    display: none;
  }

  /* Show the indicator (dot/circle) when checked */
  .address-radio-main input:checked ~ .checkmark:after {
    display: block;
  }

  /* Style the indicator (dot/circle) */
  .address-radio-main .checkmark:after {
    top: 3px;
    left: 3px;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background: #0AAF51;
  }
}

